








































import { Component } from "vue-property-decorator";
import Button from "@/components/Button.vue";
import { mixins } from "vue-class-component";
import TagHelper from "@/mixins/TagHelper";
import MyIcon from "@/views/MyIcon.vue";

@Component({
  components: { MyIcon, Button },
})
export default class Labels extends mixins(TagHelper) {
  show = false;
  open = false;
  tagName = "";
  message = "";
  color = "";
  createTag() {
    this.show = true;
  }
  colse() {
    this.show = false;
  }
  addTagNameComfirm() {
    if (this.tagName === "") {
      this.open = true;
      this.color = "";
      this.message = "标签名不能为空";
    } else if (this.tags.some((item: any) => item.name === this.tagName)) {
      this.open = true;
      this.message = "标签名重复";
      this.color = "";
    } else {
      this.$store.commit("createTag", this.tagName);
      this.show = false;
      this.open = true;
      this.message = "添加成功";
      this.color = "info";
    }
    setTimeout(() => (this.open = false), 3000);
  }
  get tags() {
    return this.$store.state.tagList;
  }

  created() {
    this.$store.commit("fetchTags");
  }
}
